import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmCaptionControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-caption-control', VmCaptionControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  

export interface CaptionControlProps extends JSX.VmCaptionControl, HTMLAttributes<HTMLVmCaptionControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmCaptionControlElement, CaptionControlProps>(
  'vm-caption-control',
  new Set(['showIcon','hideIcon','tooltipPosition','tooltipDirection','hideTooltip','icons','keys','i18n','playbackReady','textTracks','isTextTrackVisible']),
);
  